<template>

	<div class="chedui-1-3">
		<div class="chedui-top clearfix">
			<p><span>录入运单/ </span><span>说明说明说明</span></p>
			<a href="javascript:history.back(-1)">返回上一页 <span class="fa fa-angle-right"></span> </a>
		</div>
		<div class="chedui-bottom">
			<div class="jiansuo">
				<span>司机信息检索</span>
				<el-input v-model="input" placeholder="请输入姓名或车牌号或手机号检索，通用！"></el-input>
				<el-button type="primary" icon="search">搜索</el-button>
			</div>
			<div class="xinxi-box clearfix">
				<div class="xinxi-left">
					<el-form :model="numberValidateForm" ref="numberValidateForm" label-width="200px" class="demo-ruleForm">
						<el-form-item label="承运公司信息" prop="age" :rules="[{ required: true, message: '请输入公司姓名'}]">
							<el-input type="age" v-model.number="numberValidateForm.age" auto-complete="off"></el-input>
						</el-form-item>
						<el-form-item label="车牌号" prop="chepaihao" :rules="[{ required: true, message: '请输入车牌号'},{ type: 'number', message: '车牌号必须为数字值'}]">
							<el-input type="age" v-model.number="numberValidateForm.chepaihao" auto-complete="off"></el-input>
						</el-form-item>
						<el-form-item label="司机姓名" prop="name" :rules="[{ required: true, message: '请输入司机姓名'},{ type: 'string', message: '你在逗我？'}]">
							<el-input type="age" v-model.number="numberValidateForm.name" auto-complete="off"></el-input>
						</el-form-item>
						<el-form-item label="运单号" prop="yundanhao" :rules="[{ required: true, message: '请输入运单号'},{ type: 'number', message: '必须为数字值'}]">
							<el-input type="age" v-model.number="numberValidateForm.yundanhao" auto-complete="off"></el-input>
						</el-form-item>
						<el-form-item label="收票方" prop="shoupiaofang" :rules="[{ required: true, message: '此处不可空白'}]">
							<el-input type="age" v-model.number="numberValidateForm.shoupiaofang" auto-complete="off"></el-input>
						</el-form-item>
						<el-form-item label="发货人" prop="fahuoren" :rules="[{ required: true, message: '请输入发货人'}]">
							<el-input type="age" v-model.number="numberValidateForm.fahuoren" auto-complete="off"></el-input>
						</el-form-item>
						<el-form-item label="联系电话" prop="call" :rules="[{ required: true, message: '请输入手机号'},{ type: 'number', message: '无法识别！！！'}]">
							<el-input type="age" v-model.number="numberValidateForm.call" auto-complete="off"></el-input>
						</el-form-item>
						<el-form-item label="始发城市" prop="chengshi" :rules="[{ required: true, message: '请输入详细的城市'}]">
							<el-select v-model="value" placeholder="省/市/区">
								<el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
								</el-option>
							</el-select>
							<el-input placeholder="请输入详细地址" id='chengshi1' type="age" v-model.number="numberValidateForm.chengshi" auto-complete="off"></el-input>
						</el-form-item>
						<el-form-item label="收货人" prop="shouhuoren" :rules="[{ required: true, message: '请输入姓名'},{ type: 'string', message: '请输入汉字，pinyin也可以。'}]">
							<el-input type="age" v-model.number="numberValidateForm.shouhuoren" auto-complete="off"></el-input>
						</el-form-item>
						<el-form-item label="联系电话" prop="calll" :rules="[{ required: true, message: '请输入手机号'},{ type: 'number', message: '无法识别！！！'}]">
							<el-input type="age" v-model.number="numberValidateForm.calll" auto-complete="off"></el-input>
						</el-form-item>
						<el-form-item label="目的城市" prop="midi" :rules="[{ required: true, message: '请输入详细的城市'}]">
							<el-select v-model="value2" placeholder="省/市/区">
								<el-option v-for="item in options2" :key="item.value" :label="item.label" :value="item.value">
								</el-option>
							</el-select>
							<el-input placeholder="请输入详细地址" id='chengshi1' type="age" v-model.number="numberValidateForm.midi" auto-complete="off"></el-input>
						</el-form-item>
						<el-form-item label="货物名称" prop="huowuname" :rules="[{ required: true, message: '请输入货物名称'}]">
							<el-input type="age" v-model.number="numberValidateForm.huowuname" auto-complete="off"></el-input>
						</el-form-item>
						<el-form-item label="运单单价" prop="danjia" :rules="[{ required: true, message: '请输入单价'}]">
							<el-input type="age" v-model.number="numberValidateForm.danjia" auto-complete="off"></el-input>
						</el-form-item>
						<el-form-item label="运费金额" prop="jine" :rules="[{ required: true, message: '请输入运费金额'}]">
							<el-input type="age" v-model.number="numberValidateForm.jine" auto-complete="off"></el-input>
						</el-form-item>
						<el-form-item label="司机运费" prop="yunfei" :rules="[{ required: true, message: '请输入司机运费'}]">
							<el-input type="age" v-model.number="numberValidateForm.yunfei" auto-complete="off"></el-input>
						</el-form-item>

						<el-upload class="upload-demo" action="https://jsonplaceholder.typicode.com/posts/" :on-preview="handlePreview" :on-remove="handleRemove" :file-list="fileList">
							<el-form-item label="上传合同" prop="hetong">
								<el-input type="age" v-model.number="numberValidateForm.hetong" auto-complete="off"></el-input>
							</el-form-item>
						</el-upload>

						<el-upload class="upload-demo" action="https://jsonplaceholder.typicode.com/posts/" :on-preview="handlePreview" :on-remove="handleRemove" :file-list="fileList">
							<el-form-item label="上传回单" prop="huidan">
								<el-input type="age" v-model.number="numberValidateForm.huidan" auto-complete="off"></el-input>
							</el-form-item>
						</el-upload>
						
						<el-form-item label="备注" prop="beizhu" >
							<el-input type="textarea" :rows="4" placeholder="^ - ^ 欸y"></el-input>
						</el-form-item>
						
						
						<el-form-item>
							<el-button type="success" @click="submitForm('numberValidateForm')" style='padding: 10px 70px;'>保存运单</el-button>
							<el-button @click="resetForm('numberValidateForm')">清空-重置</el-button>
						</el-form-item>
					</el-form>
				</div>
				<div class="xinxi-right">
					<h2>企业历史运单</h2>
					<ul>
			
						<li v-for="(iite,index) in list">
							<el-radio class="radio" v-model="val" :label="iite.radio"><span></span></el-radio>
							<span>{{iite.name}}</span>
							<span class="fa fa-long-arrow-right"></span>
							<span>{{iite.name2}}</span>
						</li>
					</ul>
					<p>输入手机号查看司机历史纪录</p>
				</div>
			</div>
		</div>

	</div>
</template>

<script>
	export default {
		components: {

		},
		data() {
			return {
				val:"1",
				input: '',
				numberValidateForm: {
					age: '',
					chepaihao: '',
					name: '',
					yundanhao: '',
					shoupiaofang: '',
					fahuoren: '',
					call: '',
					chengshi: "",
					shouhuoren: "",
					calll: "",
					midi: "",
					huowuname: "",
					huowuage: "",
					danjia: "",
					jine: "",
					yunfei: "",
					hetong: "",
					huidan: "",
					beizhu: ""
				},
				options: [{
					value: '选项1',
					label: '你家'
				}, {
					value: '选项2',
					label: '我家'
				}, {
					value: '选项3',
					label: '他家'
				}],
				options2: [{
					value: '选项1',
					label: '你家'
				}, {
					value: '选项2',
					label: '我家'
				}, {
					value: '选项3',
					label: '他家'
				}],
				value: '',
				value2: '',
				fileList: [],
				list:[
					{name:'银川',name2:'潍坊',radio: '1',age:1},
					{name:'银川',name2:'潍坊',radio: '2',age:1},
					{name:'银川',name2:'潍坊',radio: '3',age:1},
					{name:'银川',name2:'潍坊',radio: '4',age:1},
					{name:'银川',name2:'潍坊',radio: '5',age:1},
					{name:'银川',name2:'潍坊',radio: '6',age:1}
				]
			}
		},
		methods: {
			submitForm(formName) {
				this.$refs[formName].validate((valid) => {
					if(valid) {
						alert('提交成功!');
					} else {
						console.log('error submit!!');
						return false;
					}
				});
			},
			resetForm(formName) {
				this.$refs[formName].resetFields();
			},
			handleRemove(file, fileList) {
				console.log(file, fileList);
			},
			handlePreview(file) {
				console.log(file);
			},
		}
	}
</script>

<style lang="less">
	.clearfix:after {
		display: block;
		clear: both;
		content: '';
	}
	
	.chedui-1-3 {
		.chedui-top {
			width: 1340px;
			p:nth-child(1) {
				height: 90px;
				line-height: 90px;
				color: #8F9DC0;
				float: left;
				span:nth-child(1) {
					font-size: 20px;
					font-weight: bold;
					color: #3651A2;
				}
				span:nth-child(2) {
					font-size: 14px;
				}
			}
			a {
				float: right;
				line-height: 90px;
				font-size: 14px;
				cursor: pointer;
				&:hover {
					color: #8F9DC0;
				}
				span {
					font-size: 16px;
				}
			}
		}
		.chedui-bottom {
			width: 1340px;
			background: #FFFFFF;
			border: 1px solid #CDCDCD;
			padding-bottom: 500px;
			.jiansuo {
				margin: 40px 30px 30px 30px;
				padding-bottom: 30px;
				border-bottom: 2px solid #ddd;
				>span {
					font-size: 18px;
					color: #3253AC;
				}
				.el-input {
					width: 320px;
					>input {
						height: 40px;
						background: #F5F6FB;
					}
				}
			}
			.xinxi-box {
				margin: 0 30px;
				.xinxi-left {
					float: left;
					.el-input {
						width: 320px;
					}
					.upload-demo {
						display: flex;
					}
					#chengshi1 {
						width: 215px;
					}
					.el-select {
						width: 100px;
						.el-input {
							.el-input__icon {
								left: 60px;
							}
							>input {
								width: 100px;
							}
						}
					}
				}
				.xinxi-right {
					float: right;
					width: 320px;
					border:1px solid #F8EEEF;
					background: #FFFCFD;
					padding-bottom: 300px;
					h2{
						padding: 15px 20px;
						width: 320px;
						border-bottom: 1px solid #F8EEEF;
					}
					>ul{
						width: 320px;
						padding: 20px 0;
						>li{
							height: 40px;
							line-height: 40px;
							background: #fff;
							border: 1px solid #F8EEEF;
							margin: 5px 20px;
							display: flex;
							justify-content: space-around;
							>span:nth-child(3){
								color: #FFA099;
								font-size: 25px;
								padding-top: 7px;
							}
						}
					}
					>p{
						margin-left: 20px;
						color: #ccc;
					}
				}
			}
		}
	}
</style>


